import { ethers } from "ethers";

declare global {
    interface Window {
        ethereum: any;
    }
}

const getWeb3 = async () => {
    if( window.ethereum ) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
    }
    else
        throw new Error('Metamask needs to be installed!!!');
};

